@keyframes buzz-out {
  10% {
    transform: scale(1.2) translateX(3px) rotate(10deg);
  }

  20% {
    transform: scale(1.2) translateX(-3px) rotate(-10deg);
  }

  30% {
    transform: scale(1.2) translateX(3px) rotate(10deg);
  }

  40% {
    transform: scale(1.2) translateX(-3px) rotate(-10deg);
  }

  50% {
    transform: scale(1.2) translateX(2px) rotate(5deg);
  }

  60% {
    transform: scale(1.2) translateX(-2px) rotate(-5deg);
  }

  70% {
    transform: scale(1.2) translateX(2px) rotate(5deg);
  }

  80% {
    transform: scale(1.2) translateX(-2px) rotate(-5deg);
  }

  90% {
    transform: scale(1.2) translateX(1px) rotate(0);
  }

  100% {
    transform: scale(1) translateX(-1px) rotate(0);
  }
}


@keyframes push {
  50% {
    transform: scale(0.9);
  }

  100% {
    transform: scale(1);
  }
}

@mixin anim-push {
  animation-name: push;
  animation-duration: $default-transition;
  animation-timing-function: ease-out;
  animation-iteration-count: 1;
}

@mixin anim-buzz-out {
  animation-name: buzz-out;
  animation-duration: $default-transition;
  animation-timing-function: linear;
  animation-iteration-count: 1;
}

@mixin anim-shutter-out-horizontal {
  position: relative;
  z-index: 0;
  transition: color $default-transition;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: $color-blue;
    transform: scaleX(0);
    transition: transform $default-transition ease-out;
  }

  &:hover,
  &:focus,
  &:active {
    color: white;

    &::before {
      transform: scaleX(1);
    }
  }
}


@mixin anim-underline-from-center {
  &::before {
    content: "";
    position: absolute;
    z-index: 5;
    left: 51%;
    right: 51%;
    bottom: 0;
    background: $color-blue;
    height: 2px;
    transition: $default-transition ease-out;
  }
}
